@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
html, body {
  background-image: linear-gradient(to bottom right, #fff, #f0f0f0);
  height: 100%;
  font-family: 'Montserrat', sans-serif;
}

button {
  font-family: 'Montserrat', sans-serif;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video, button {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
    vertical-align: baseline;
}
.App {
  height: 100vh;
  display: grid;
  grid-template-rows: repeat(10, 1fr);
  grid-template-columns: 5% auto 5%;
  font-weight: 600;
  letter-spacing: 1px;
}

button {
  cursor: pointer;
}

button:focus {
  outline: none;
}

.App > * {
  grid-column: 2 / 3;
}
#contact {
  background-color: #fff;
  box-shadow: 0px 0px 17px -7px rgba(0,0,0,0.75);
  border: none;
  border-radius: 25px;
  padding: 0px 20px 0 0px;
  margin: 15px 0 0 0;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  align-self: flex-start;
  justify-self: flex-end;
  font-weight: 600;
  letter-spacing: 1px;

  background-position: center;
  transition: background 0.8s;
}

#contact a {
  display: flex;
  align-items: center;
  text-decoration: none;  
  color: #000;
}

#contact:hover {
  background: rgb(233, 233, 233) radial-gradient(circle, transparent 1%, rgb(233, 233, 233) 1%) center/15000%;
}

#contact:active {
  background-color: #f0f0f0;
  background-size: 100%;
  transition: background 0s;
}

#contact img {
  border-radius: 50%;
  height: 50px;
  margin-right: 5px;
}

#responsive-info {
  background-image: linear-gradient(to right, #FFDC87, #FFB758);
  padding: 10px 15px;
  align-self: flex-end;
  justify-self: center;
  border-radius: 5px;
}
#personal-info {
  grid-row: 3 / 10;
  align-self: center;
}

.big-font-size {
  font-size: 4em;
}

#skills {
  display: flex;
}

#skills p {
  color: #fff;
  font-size: 2em;
  padding: 10px 20px;
  margin: 20px 20px 0 0;
  border-radius: 5px;
}

#skills p:nth-child(1) {
  background-image: linear-gradient(to right, #afafaf, #8d8d8d);
}

#skills p:nth-child(2) {
  background-image: linear-gradient(to right, #8d8d8d, #797979);
}

#skills p:nth-child(3) {
  background-image: linear-gradient(to right, #797979, #525252);
}

#icons {
  align-self: flex-end;
  justify-self: center;
  margin-bottom: 15px;
  display: flex;
}
#icons button {
  background-color: transparent;
  margin-right: 20px;
}
#icons button img {
  transition: opacity 0.5s;
  border-radius: 50%;
  height: 25px;
  opacity: .5;
}

#icons button img:hover {
  opacity: 1;
}
